.service {
  height: 100%;
  font-family: "Open Sans", sans-serif, Arial, Helvetica;
  padding-bottom: 100px;
}

.service-header {
  font-size: 30px;
  text-align: center;
  padding: 60px 50px;
  font-weight: bolder;
}
.b--black-10 {
  background-color: whitesmoke;
}
.tachyon-check {
  padding: 32px 16px 16px;
}
.headind-h3 {
  font-size: 20px;
}
p {
  font-size: 18px;
}
.circle1,
.head1 {
  color: rgb(98, 117, 179);
}
.circle2,
.head2 {
  color: rgb(100, 175, 100);
}
.circle3,
.head3 {
  color: rgb(177, 177, 104);
}

@media (max-width: 768px) {
  .service-header {
    font-size: 20px;
    padding: 20px 10px 15px;
  }

  .tachyon-service {
    margin: 4px 0px;
  }
  .tachyon-check {
    padding-top: 5px;
  }
  .headind-h3 {
    font-size: 18px;
  }

  p {
    font-size: 15px;
  }
  .service {
    padding-bottom: 60px;
    width: 100vw;
  }
}
