.servicehomemobile {
  font-family: "Open Sans", sans-serif, Arial, Helvetica;
  width: 100%;
  padding: 0px;
}

.webmobile-h1,
.exportmobile-h1,
.importmobile-h1,
.digitalmobile-h1 {
  padding: 30px 0 0px;
  text-align: center;

  font-size: 18px;
}
.webmobile-p,
.exportmobile-p,
.importmobile-p,
.digitalmobile-p {
  padding: 10px 20px;

  font-size: 15px;
}

.serrowmobile {
  margin: 0px;
}
div.col-md-6 {
  padding: 0px;
}
div.text {
  background-color: whitesmoke;
}

.div-moremobile {
  text-align: center;
  padding: 20px;
}
.moremobile {
  background-color: rgb(182, 178, 178);
  color: white;
  padding: 10px 25px;
  font-size: 20px;
  opacity: 0.8;

  border-radius: 20px;
}

div.col-md-6.text {
  padding-bottom: 50px;
}

@media (min-width: 768px) {
  .servicehomemobile {
    display: none;
  }
}
