div.container-fluid.home {
  background-color: black;

  text-align: center;
  height: 100vh;
  z-index: 1;
  top: 0;
  left: 0;
  color: white;
  background-image: linear-gradient(
      90deg,
      rgba(0, 0, 0, 0.6),
      rgba(0, 0, 0, 0.6)
    ),
    url("../images/stock1.jpg");
  background-position: center; /* Center the image */
  background-repeat: no-repeat; /* Do not repeat the image */
  background-size: cover; /* Resize the background image to cover the entire container */
}
.home-content {
  font-size: 50px;
  font-family: "Trykker", serif;
  font-weight: lighter;
  padding: 120px 10px 0;
  max-width: 1000px;
  margin: auto;
}
.home-content-p {
  max-width: 850px;
  margin: auto;
  font-family: "Trykker", serif;
  padding: 50px 10px 0;
  text-align: center;
  font-size: 20px;
}

.home-body {
  font-family: "Open Sans", sans-serif, Arial, Helvetica;
  text-align: center;
  padding-top: 20px;
}
.home-body-content {
  max-width: 800px;
  margin: 0 auto;
  padding: 25px 20px 60px;

  font-size: 20px;

  text-align: left;
}

.home-body-h1 {
  text-align: center;
}
.home-body-p {
  font-size: 18px;
}

@media (max-width: 768px) {
  div.container-fluid.home {
    height: 100%;
    padding: 0px 0px 100px;
    text-align: center;
  }
  .home-content {
    font-size: 30px;
    padding: 55px 10px 20px;
  }
  .home-content-p {
    padding: 50px 10px 18px;
    text-align: center;
    font-size: 18px;
  }
  .home-body-content {
    padding: 5px 20px 20px;
  }
  .home-body-h1 {
    font-size: 18px;
    text-align: center;
    padding: 10px;
  }
  .home-body-p {
    font-size: 15px;
  }
  .fa-crown {
    font-size: 30px;
    padding: 10px 0px 0px;
  }
}

.home-content {
  position: relative;
  animation-name: head;
  animation-duration: 4s;

  animation-fill-mode: forwards;
}
