div.container-fluid.contactall {
  padding: 30px 15px 70px;

  display: flex;
  flex-direction: column;
  justify-content: center;
  background-image: linear-gradient(
      90deg,
      rgba(70, 67, 67, 0.6),
      rgba(180, 175, 175, 0.6)
    ),
    url("../images/world.jpg");
  background-position: center; /* Center the image */
  background-repeat: no-repeat; /* Do not repeat the image */
  background-size: cover; /* Resize the background image to cover the entire container */
}

.contactall {
  font-family: "Open Sans", sans-serif, Arial, Helvetica;
  padding: 0 0 5% 0;
  background-color: rgb(5, 79, 92);
  color: white;
}

.contactall-p {
  display: flex;
  flex-direction: column;
  align-items: center;

  padding: 50px 0 0 5px;
}
.contactall-paragraph {
  text-align: left;
  padding: 50px;
  max-width: 700px;
  margin: 0 auto;

  font-size: 18px;
}
p.contactall-paragraph {
  padding: 20px 10px;
}
.getstarted-div {
  padding: 20px 10px;

  margin: 0 auto;
}
.getstarted {
  background-color: rgb(8, 52, 61);
  color: white;
  padding: 10px 20px;

  font-size: 18px;
  border-radius: 10px;
}

.faaa {
  padding: 5px;
  font-size: 5px;
  width: 30px;
  text-align: center;
  text-decoration: none;
  text-align: center;
  border-radius: 5px;
}

.fa-linkedin:hover {
  color: #007bb5;
}
.fa-facebook:hover {
  color: #3b5998;
}
.fa-instagram:hover {
  color: #881257;
}
.fa-twitter:hover {
  color: #55acee;
}

@media (max-width: 768px) {
  div.container-fluid.contactall {
    padding: 60px 15px 90px;
    border-bottom: 1px solid white;
    height: 100%;
  }
  .contactall {
    padding: 40px 0px 100px 0px;
    width: 100vw;
  }
  .contactall-p {
    padding: 15px;

    font-size: 20px;
  }
  .contactall-paragraph {
    text-align: left;
    padding: 50px 10px;
    font-size: 15px;
  }
  .getstarted-div {
    padding: 20px 10px;
  }
  .getstarted {
    padding: 10px;

    font-size: 15px;
  }
}
