.toolbar {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  box-shadow: 0px 0px 5px black;
  background-color: white;
  z-index: 99;
  height: 60px;
}

.toolbar__navigation {
  display: flex;
  height: 100%;
  align-items: center;
  padding: 0 1rem;
}

.spacer {
  flex: 1;
}
.toolbar__logo a {
  padding-left: 60px;
}

.toolbar_navigation-items {
  color: black;
  text-decoration: none;
  font-size: 20px;
  padding: 0 3rem;
}

.toolbar_navigation-items:hover {
  color: rgb(61, 128, 141);
}
.toolbar_navigation-items:focus {
  color: rgb(23, 126, 223);
}

@media (min-width: 769px) {
  .toolbar__toggle-button {
    display: none;
  }

  .toolbar__logo {
    margin-left: 0;
  }
}
@media (max-width: 769px) {
  .toolbar__logo a {
    padding-left: 0px;
  }
}
