.servicehome {
  font-family: "Open Sans", sans-serif, Arial, Helvetica;
  width: 100%;
  padding: 0px;
}

.web-h1,
.export-h1,
.import-h1,
.digital-h1 {
  padding: 80px 0 50px;
  text-align: center;
}
.web-p,
.export-p,
.import-p,
.digital-p {
  padding: 0 20px;

  font-size: 18px;
}

.serrow {
  margin: 0px;
}
div.text-col,
div.img-col {
  padding: 0px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
}
div.text-col {
  background-color: whitesmoke;
}

.div-more {
  text-align: center;
  padding: 50px;
}
.more {
  background-color: rgb(182, 178, 178);
  color: white;
  padding: 13px 25px;
  font-size: 20px;
  opacity: 0.8;

  border-radius: 20px;
}
/*
.more:hover {
  background-color: white;
  color: saddlebrown;
  border: solid saddlebrown;
} */
.fa-long-arrow-right:hover {
  color: white;
  background-color: rgb(182, 178, 178);
}

@media (max-width: 768px) {
  .servicehome {
    display: none;
  }
}
