.about {
  font-family: "Open Sans", sans-serif, Arial, Helvetica;

  text-align: center;
  height: 100%;
  padding: 5% 5%;
  background-image: linear-gradient(
      90deg,
      rgba(70, 67, 67, 0.6),
      rgba(180, 175, 175, 0.6)
    ),
    url("../images/sky.jpg");
  background-position: center; /* Center the image */
  background-repeat: no-repeat; /* Do not repeat the image */
  background-size: cover; /* Resize the background image to cover the entire container */
  color: white;
}
.about-container {
  background-color: rgba(37, 37, 39, 0.65);
  max-width: 750px;
  margin: 0 auto;
}
.founder-section {
  padding: 40px 0px;
}
.about-section {
  padding: 40px 0px 0px;
}

.about-head {
  padding: 0px 0 30px;

  font-weight: bolder;
  font-size: 30px;
}

.about-p {
  text-align: left;
  max-width: 700px;
  margin: 0 auto;

  padding: 20px;
}
.p-element {
  font-size: 18px;
}
.misvision {
  padding: 20px 10px;
}
.misvision-span {
  font-size: 20px;
  font-weight: bold;
}
.misvision-p {
  font-size: 18px;
  max-width: 700px;
  margin: 0 auto;
}

@media (max-width: 768px) {
  .about {
    width: 100vw;
    padding: 5% 5% 5% 5%;
  }
  .about-container {
    margin: 0 0 15px;
  }
  .about-img {
    width: 150px;
  }
  .founder-section {
    padding: 25px 0px;
  }
  .about-head {
    padding: 0px 0px 10px;
    font-size: 20px;
    margin: 0;
  }

  .about-p {
    text-align: left;
    margin: 0 auto;
    padding: 20px 20px 0;
  }
  .p-element {
    font-size: 15px;
  }
  .misvision {
    padding: 10px;
    border-radius: 50px;
  }
  .misvision-span {
    font-size: 18px;
  }
  .misvision-p {
    font-size: 15px;
  }
}
